@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap');

*{
  font-family: 'Quicksand', sans-serif;
  color: #333;
}


.App{
  width: 800px;
  margin: 0 auto;
}

.navbar{
  display: flex;
  align-items: center;
}

.navbar h1{
  color:#f2345f;
}

.links{
  margin-left: auto;
}

.links a{
  text-decoration: none;
  margin-left: 30px;
}

.links a:hover{
  color: #f2345f;
}

.remove-link-style{
  text-decoration: none;
}

/* new blog */
.new-blog{
  max-width: 600px;
}

.new-blog label,input,textarea,select{
  display: block;
  width: 100%;
  margin-bottom: 10px;
  font-size: 16px;
  border-radius: 5px;
  padding: 5px;
}

.new-blog input,textarea,select{
  border: 1px solid rgb(194, 194, 194);
}

.new-blog button{
  background-color: #f2345f;
  border : 0;
  border-radius : 8px;
  color : #fff;
  padding : 5px;
  font-size: 16px;
  display: block;
  margin: 20px auto;
}



/* blog-details */

.blog-details button{
  background-color: #f2345f;
  border : 0;
  border-radius : 8px;
  color : #fff;
  padding : 5px;
  font-size: 16px;
  cursor: pointer;
}

.theblog{
  padding: 10px;
  border: 1px solid rgb(218, 218, 218);
  border-radius: 5px;
  margin: 5px 0;
}

.theblog h2,p{
  margin: 2px 0;
}

@media only screen and (max-width: 850px){
  .App{
    width: auto;
    margin: 0 1rem;
  }

  .navbar h1 {
    font-size: 1.1rem;
  }

  .blog-details p{
    margin-bottom: 0.5rem;
  }
}